body {
    transition: background-color 1.5s ease-in-out;
}
.quote {
transform: translateY(0);
transition:  0.5s ease-in-out, transform 0.5s ease-in-out ;
}
.quote-enter {
    opacity: 0;
    transform: translateY(-20px);
}
.quote-leave {
    opacity: 0;
    transform: translateY(20px);
}